@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(14, 17, 23);
}

h1, p, button {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
  color: white;
  margin-top: 15px;
}

p {
  font-weight: 400;
  margin-bottom: 5px;
  overflow-y: hidden;
}

button {
  width: 150px;
  height: 50px;
  border-radius: 20px;
  background-color: mediumpurple;
  outline: none;
  border: 2px solid mediumpurple;
  font-size: 20px;
  font-weight: 600;
  margin-top: 25px;
  cursor: pointer;
  transition: 0.3s;
  transform: translateY(0px);
  box-shadow: rgba(255, 255, 255, 0.15) 4px 4px 4px;
}

button:hover {
  background-color: mediumslateblue;
  border: 2px solid mediumslateblue;
  transition: 0.3s;
  transform: translateY(-2px);
}

.code-editor {
  width: 400px;
  height: 400px;
  box-shadow: rgba(255, 255, 255, 0.15) 3px 3px 2.6px;
}

#input {
  margin-left: 25px;
  float: left;
}

#output {
  margin-right: 25px;
  float: right;
}

select {
  background-color: #222222;
  outline: none;
  border: 1px solid white;
  color: white;
  margin-left: 10px;
}

#footer {
  margin-top: calc(60vh);
}

.footer-p {
  color: rgba(255, 255, 255, 0.3);
  font-size: 10px;
}